import React, { Component } from 'react'
import Dial from './Dial'
import './Dial.css'
import './Clock.css'

class Clock extends Component {
    constructor(props) {
        super(props)

        this.dialRef = React.createRef()

        this.state = {
            startedCallback: this.props.startedCallback,
            pauseCallback: this.props.pauseCallback,
            completedCallback: this.props.completedCallback,
            poms: this.props.poms,
            currentPom: this.props.currentPom,
            running: this.props.running,
            timeLeft: -1,
            dialKey: 0
        }

        this.handleStartButtonClick = this.handleStartButtonClick.bind(this)     
        this.clockCompletedCallback = this.clockCompletedCallback.bind(this)     
    }
    
    handleStartButtonClick(event) {
        if (this.state.poms.length > 0) {
            if (this.state.running) {
                this.setState({running: false})
                this.setState({timeLeft: this.timerRef.current.state.timeLeft})
            } else { 
                this.setState({running: true})
                this.state.startedCallback()
            }
            
            this.setState({dialKey: this.state.dialKey + 1})
        }
        
        event.preventDefault()
    }

    clockCompletedCallback() {
        this.setState({currentPom: this.state.currentPom + 1})
        this.setState({timerKey: this.state.timerKey + 1})
        
        this.state.completedCallback()
    }

    render() {
        return (
            <>
                <Dial
                    key={this.state.dialKey}
                    running={this.state.running}
                    poms={this.state.poms}
                    currentPom={this.state.currentPom}
                    timeLeft={this.state.timeLeft}
                    startedCallback={this.state.startedCallback}
                    completedCallback={this.clockCompletedCallback}
                    ref={this.dialRef}
                />
            </>
        )
    }
}

export default Clock