import React, { Component } from 'react'

class DialControls extends Component {
    constructor(props) {
        super(props)

        this.state = {
            startCallback: this.props.startCallback,
            running: this.props.running
        }

        this.handleStartClick = this.handleStartClick.bind(this)
    }

    handleStartClick(event) {
        event.preventDefault()
        
        this.state.startCallback()
    }

    setStartButtonText() {
        let buttonLabel = document.getElementById('btn-label')

        if (this.state.running) {
            buttonLabel.innerText = "Pause"
        } else {
            buttonLabel.innerText = "Start"
        }
    }

    componentDidMount() {
        this.setStartButtonText()
    }

    render() {
        return (
        <div>
            <button id="start-btn" onClick={this.handleStartClick}>
                <label id="btn-label">Start</label>
            </button>
        </div>
        )
    }
}

export default DialControls