// https://css-tricks.com/how-to-create-an-animated-countdown-timer-with-html-css-and-javascript/

import React, { Component } from 'react'
import DialControls from './DialControls'


const FULL_DASH_ARRAY = 283


class Dial extends Component {
  constructor(props) {
    super(props)

    this.state = {
      poms: this.props.poms,
      currentPom: this.props.currentPom,
      startedCallback: this.props.startedCallback,
      completedCallback: this.props.completedCallback,
      started: false,
      running: this.props.running,
      timeLeft: this.props.timeLeft,
      timerInterval: null,
      dialControlsKey: 0
    }

    this.startTimer = this.startTimer.bind(this)
  }

  formattedTime() {
    if (this.state.timeLeft < 0)
      return "--:--"

    const minutes = Math.floor(this.state.timeLeft / 60)
    let seconds = this.state.timeLeft % 60

    if (seconds < 10) {
      seconds = '0' + seconds
    }
  
    return minutes + ':' + seconds
  }

  startTimer() { 
    let currentPom = this.state.currentPom

    if (this.state.currentPom < 0) {
      currentPom++
      this.setState({currentPom: currentPom})
    }

    if (this.state.timerInterval === null) {    
      this.state.startedCallback()
      this.setState({running: true})

      let timerInterval = setInterval(() => {
        let duration = this.state.poms[currentPom]
        let timeLeft = this.state.timeLeft
      
        if (timeLeft === -1) {
          timeLeft = duration
          this.setState({timeLeft: timeLeft})
        }

      this.setState({duration: duration})
        if (this.state.running) {
          
          if (timeLeft <= 0) {
            this.doComplete()
          } else {
            if (timeLeft >= duration) {
              this.doBegin()
            }
            else {
              let timerRemainingPath = document.getElementsByClassName('base-timer__path-remaining')[0]
              timerRemainingPath.classList.add('transition')
              document.getElementById('timer-label').innerHTML = this.formattedTime()
              this.setCircleDasharray()
              this.setState({timeLeft: this.state.timeLeft - 1})
            }
          }
        }
      }, 1000)
      
      this.setState({timerInterval: timerInterval})
    } else {
      this.setState({running: !this.state.running})
      this.setState({dialControlsKey: this.state.dialControlsKey + 1})
    }
  }
      
  doBegin() {
    this.state.startedCallback()
    this.setFullCircleDasharrray()
    this.setState({timeLeft: this.state.timeLeft - 1})
  }
      
  doComplete() {
    if (this.state.timerInterval != null) {
      clearInterval(this.state.timerInterval)
    }
    
    let timerRemainingPath = document.getElementsByClassName('base-timer__path-remaining')[0]
    timerRemainingPath.style.removeProperty('stroke')
    timerRemainingPath.classList.remove('transition')

    let elapsedPath = document.getElementById('base-timer-path-elapsed')
    elapsedPath.classList.add('completed-blink')

    let blinkInterval = setInterval(() => {
      elapsedPath.classList.remove('completed-blink')
      this.setState({timeLeft: 0})
      this.startTimer()

      clearInterval(blinkInterval)

      this.state.completedCallback()
    }, 5000)
  }

  componentDidMount() {
    if (this.state.timerInterval != null) {
      clearInterval(this.state.timerInterval)
    }
    
    if (this.state.running) {
      if (this.state.timeLeft > 0)
        this.setCircleDasharrayNoAnimation()
      
      this.startTimer()
    } else {
      let timeLabel = document.getElementById('timer-label')
      let timeRemainingPath = document.getElementById('base-timer-path-remaining')
      let timeElapsedPath = document.getElementById('base-timer-path-elapsed')

      timeLabel.classList.add('paused')
      timeRemainingPath.classList.add('paused')
      timeElapsedPath.classList.add('paused-background')

      this.setCircleDasharray()
    }
  }

  componentWillUnmount() {
    if (this.state.timerInterval != null) {
      clearInterval(this.state.timerInterval)
    }
  }

  calculateTimeFraction() {
    const rawTimeFraction = this.state.timeLeft / this.state.duration
    return rawTimeFraction - ((1 - rawTimeFraction) / (this.state.duration - 1))
  }

  setFullCircleDasharrray() {
    const circleDasharray = `${(
      FULL_DASH_ARRAY
    ).toFixed(0)} 283`

    document.getElementById("base-timer-path-remaining").setAttribute("stroke-dasharray", circleDasharray)
  }

  setCircleDasharrayNoAnimation() {
    let timerRemainingPath = document.getElementsByClassName('base-timer__path-remaining')[0]
    timerRemainingPath.style.removeProperty('stroke')
    timerRemainingPath.classList.remove('transition')

    this.setCircleDasharray()
  }

  setCircleDasharray() {
    const circleDasharray = `${(
      this.calculateTimeFraction() * FULL_DASH_ARRAY
    ).toFixed(0)} 283`

    document.getElementById("base-timer-path-remaining").setAttribute("stroke-dasharray", circleDasharray)
  }

  render() {
      return (
          <div className="base-timer">
              <svg className="base-timer__svg" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
                  <g className="base-timer__circle">
                  <circle id="base-timer-path-elapsed" className="base-timer__path-elapsed" cx="50" cy="50" r="45" />
                  <path
                      id="base-timer-path-remaining"
                      strokeDasharray="283"
                      className={'base-timer__path-remaining'}
                      d="
                      M 50, 50
                      m -45, 0
                      a 45,45 0 1,0 90,0
                      a 45,45 0 1,0 -90,0
                      "
                  ></path>
                  </g>
              </svg>
              <div id="timer-label" className="base-timer__label">
                  {this.formattedTime()}
              </div>
              <div id="timer-controls" className="base-timer__label">
                  <DialControls
                    key={this.state.dialControlsKey}
                    startCallback={this.startTimer}
                    running={this.state.running}
                  ></DialControls>
              </div>
          </div>
      )
  }
}

export default Dial 
