import React, { Component } from 'react'
import './Schedule.css'

class Schedule extends Component {
    constructor(props) {
        super(props)

        this.state  = {
            poms: this.props.poms,
            currentPom: this.props.currentPom,
            updatePoms: this.props.updatePoms,
            longDuration: 1500,
            shortDuration: 300
        }

        this.handleLongDurationChange = this.handleLongDurationChange.bind(this)
        this.handleShortDurationChange = this.handleShortDurationChange.bind(this)
        this.handleAddLongPom = this.handleAddLongPom.bind(this)
        this.handleAddShortPom = this.handleAddShortPom.bind(this)
        this.updatePoms = this.updatePoms.bind(this)
    }

    displayPoms() {
        let poms = document.getElementById('poms')
        poms.innerHTML = ''

        for (var i = 0; i < this.state.poms.length; i++) {
            let pom = document.createElement('span')

            pom.classList.add('pom', 'new')

            if (i === this.state.currentPom) {
                pom.classList.add('pom', 'current')
            }

            pom.innerText = this.state.poms[i]

            poms.appendChild(pom)
        }
    }

    handleLongDurationChange(event) {
        this.setState({longDuration: event.target.value})
    }

    handleShortDurationChange(event) {
        this.setState({shortDuration: event.target.value})
    }

    handleAddLongPom() {
        let poms = this.state.poms
        poms.push(this.state.longDuration)
        this.setState({poms: poms})
        
        this.updatePoms()
    }
    
    handleAddShortPom() {
        let poms = this.state.poms
        poms.push(this.state.shortDuration)
        this.setState({poms: poms})
        
        this.updatePoms()
    }

    updatePoms() {
        this.props.updatePoms(this.state.poms)
        
        this.displayPoms()
    }

    handleStart() {
        let currentPom = this.props.currentPom
        let currentPomSpan = document.getElementsByClassName('pom')[currentPom]

        currentPomSpan.classList.remove('new')
        currentPomSpan.classList.add('in-progress')
    }

    componentDidMount() {
        this.displayPoms()
    }

    render() {
        return (
            <>
                <div id="schedule-container">
                    <div className="input">
                        <span className="label">Long</span>
                        <input type="text" name="long" value={this.state.longDuration} onChange={this.handleLongDurationChange}/>
                        <button onClick={this.handleAddLongPom}>+</button>

                        <span className="label">Short</span>
                        <input type="text" name="short" value={this.state.shortDuration} onChange={this.handleShortDurationChange}/>
                        <button onClick={this.handleAddShortPom}>+</button>
                    </div>
                    <div className="display">
                        <span id="poms"></span>
                    </div>
                </div>
            </>
        )
    }
}

export default Schedule