import React, { Component } from 'react'
import Clock from './Clock'
import Schedule from './Schedule'
import ReactGA from 'react-ga'

class App extends Component {
  constructor(props) {
    super(props)

    ReactGA.initialize('UA-168751680-1')
    ReactGA.pageview(window.location.pathname + window.location.search)

    this.state = {
      poms: [],
      currentPom: -1,
      running: false,
      scheduleKey: 0,
      clockKey: 0
    }

    this.updatePoms = this.updatePoms.bind(this)
    this.timerStartedCallback = this.timerStartedCallback.bind(this)
    this.timerPausedCallback = this.timerPausedCallback.bind(this)
    this.timerCompletedCallback = this.timerCompletedCallback.bind(this)
  }

  updatePoms(poms) {
    this.setState({poms: poms})
  }

  timerStartedCallback() {
    if (this.state.currentPom < 0) {
      this.setState({running: !this.state.running})
      this.setState({currentPom: this.state.currentPom + 1})
      this.setState({scheduleKey: this.state.scheduleKey + 1})
      this.setState({clockKey: this.state.clockKey + 1})
    }
  }

  timerPausedCallback(timeRemaining) {
    console.log(timeRemaining) 
  }

  timerCompletedCallback() {
    this.setState({currentPom: this.state.currentPom + 1})
    this.setState({scheduleKey: this.state.scheduleKey + 1})
    this.setState({clockKey: this.state.clockKey + 1})
  }

  render() {
    return (
      <div id="container">
        <div id="schedule">
          <Schedule 
            key={this.state.scheduleKey} 
            poms={this.state.poms} 
            currentPom={this.state.currentPom} 
            updatePoms={this.updatePoms}
          />
        </div>
        <div id="clock">
          <Clock 
            key={this.state.clockKey}
            poms={this.state.poms}
            currentPom={this.state.currentPom}
            running={this.state.running}
            startedCallback={this.timerStartedCallback}
            pausedCallback={this.timerPausedCallback}
            completedCallback={this.timerCompletedCallback}
          />
        </div>
      </div>
    )
  }
}

export default App;
